@import "index";
$border: thin grey solid;
.wrapper {
}
.photoshop {
  min-height: 100vh;
  &__container {
    margin: 0 auto;
    padding: 2rem 0.75rem;
    max-width: 1600px;
  }

  &__row {
    @include media-wm($normal - 1px) {
      flex-direction: column;
    }
    display: flex;

    border: $border;
    border-radius: 0.25rem;
  }

  &__image-and-range {
    flex: auto;
    @include media-wp($normal) {
      border-right: $border;
    }

    padding: 0.75rem;
    padding-bottom: 0;
    @include media-wm($normal - 1px) {
      padding-bottom: 0;
      margin-bottom: 0.5rem;
    }
  }

  &__image-layout {
    position: relative;
    &:after {
      content: "";
      display: block;
      width: 100%;
      padding-top: (9 / 16) * 100%;
    }
  }

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    width: 100%;
    height: 100%;

    object-fit: contain;
    object-position: center;
  }

  &__range-layout {
  }

  &__range {
    width: 100%;
  }

  &__sidebar {
    @include media-wp($normal) {
      flex: 0 1 150px;

      flex-direction: column;
      align-items: stretch;
    }
    flex-wrap: wrap;
    align-items: flex-start;
    display: flex;

    padding: 0.75rem;
    @include media-wm($normal - 1px) {
      padding-top: 0;
    }
  }
}
