@import "index";
.btn {
  display: inline-block;
}

.photoshop__btn {
  font-size: 0.55rem;
  @include media-wp($normal) {
    font-size: 1rem;
    padding: 0.5rem 0.75rem;
  }

  padding: 0.25rem 0.35rem;
  margin-bottom: 0.5rem;

  color: grey;

  background-color: transparent;
  border: thin grey solid;
  border-radius: 0.25rem;
  &:hover {
    color: white;
  }
  &._active {
    color: white;
    background-color: lighten($color: #1e1e1e, $amount: 20);
  }
}
